import React, { Component } from 'react'

export default class calculator extends Component {
  state = {
      output: '',
      clear: 'AC',
      length: 0,
      symbol: true,
      decimal: false
  }

  displayUpdate = (e) => {
    console.log(e.target.value)
    // clear text section
    if (e.target.value === 'clear') {
      if (this.state.clear === 'C') {
        this.setState({
          output: this.state.output.split('').slice(0,this.state.length-1).join(''),
          length: this.state.length - 1
        })
        if (this.state.length === 1) {
          this.setState({
            clear: 'AC'
          })
          console.log('Memory Cleared')
        }
      } 
    }
    // adding to display
    else {
      // detect symbols and allow for only one to be added - excluding percent and equality
      if (this.state.length <= 21 && isNaN(e.target.value) === true && e.target.value !== '=' && e.target.value !== '%' && e.target.value !== 'negate' && e.target.value !== '.') {
        if (this.state.symbol === false) {
          this.setState({
            symbol: true,
            output: this.state.output + e.target.value,
            length: this.state.length + 1,
            decimal: false
          }) 
        }
        else {
          this.setState({
            output: this.state.output.split('').slice(0,this.state.output.length-1).join('') + e.target.value
          })
        }
      }
      // handle decimal
      else if (e.target.value === '.') {
        if (this.state.decimal === false) {
          this.setState({
            output: this.state.output + e.target.value,
            length: this.state.length + 1,
            decimal: true
          })
        }
      }
      // detect numbers
      else if (this.state.length < 20  && isNaN(e.target.value) === false) {
        if (this.state.output[this.state.output.length - 1] === '%') {
          this.setState({
            output: this.state.output + '*' + e.target.value,
            length: this.state.length + 1,
            symbol: false,
            clear: 'C'
          })
        }
        else {
          this.setState({
            output: this.state.output + e.target.value,
            length: this.state.length + 1,
            symbol: false,
            clear: 'C'
          })
        }
      }
      // handle percents
      else if (this.state.length <= 21 && e.target.value === '%' && isNaN(this.state.output[this.state.output.length-1]) === false) {
        this.setState({
          output: this.state.output + e.target.value,
          length: this.state.length + 1,
          symbol: false,
          clear: 'C'
        })
      }
      // handle negation
      else if (this.state.length <= 22 && e.target.value === 'negate') {
        if (this.state.output[0] === '-') {
          this.setState({
            output: this.state.output.split('').slice(1).join(''),
            length: this.state.length - 1,
            clear: 'C'
          })
        }
        else {
          this.setState({          
            output: '-' + this.state.output,
            length: this.state.length + 1,
            clear: 'C'
          })
        }
      }
      // handle equality
      if (e.target.value === '=') {
        let buffer = this.state.output;
        if (buffer.includes('%')) {
          buffer = buffer.replace(/%/g, '/100');
        }
        if (this.state.symbol === false) {
          // eslint-disable-next-line no-eval
          buffer = `${eval(buffer)}`;
        }
        if (this.state.symbol === true) {
          this.setState ({
            output: this.state.output
          })
        }
        else {
          this.setState ({
            // eslint-disable-next-line no-eval
            output: `${eval(buffer)}`,
            length: buffer.length,
            clear: 'C',
            decimal: false
          })
        }
      }
    }
  }

  render() {
    return (
      <div id="calculator">
        <div id="display">
          <p id='displaytext'>{this.state.output}</p>
        </div>
        <div id="row">
          <button onClick={this.displayUpdate} id="clear" value="clear">{this.state.clear}</button>
          <button onClick={this.displayUpdate} value="negate">+/-</button>
          <button onClick={this.displayUpdate} value='%'>%</button>
          <button onClick={this.displayUpdate} value="/">รท</button>
        </div>
        <div id="row">
          <button onClick={this.displayUpdate} value="7">7</button>
          <button onClick={this.displayUpdate} value="8">8</button>
          <button onClick={this.displayUpdate} value="9">9</button>
          <button onClick={this.displayUpdate} value="*">x</button>
        </div>
        <div id="row">
          <button onClick={this.displayUpdate} value="4">4</button>
          <button onClick={this.displayUpdate} value="5">5</button>
          <button onClick={this.displayUpdate} value="6">6</button>
          <button onClick={this.displayUpdate} value="-">-</button>
        </div>
        <div id="row">
          <button onClick={this.displayUpdate} value="1">1</button>
          <button onClick={this.displayUpdate} value="2">2</button>
          <button onClick={this.displayUpdate} value="3">3</button>
          <button onClick={this.displayUpdate} value="+">+</button>
        </div>
        <div id="row">
          <button onClick={this.displayUpdate} value="0">0</button>
          <button onClick={this.displayUpdate} value=".">.</button>
          <button onClick={this.displayUpdate}  value="=">=</button>
        </div>
      </div>
    )
  }
}
